const mapStateToProps = ({ themeReducer: { themeColors }, uiReducer: { dialog } }) => ({
  themeColors,
  dialog
});

const mapDispatchToProps = {
  changeWindowDimensions,
  checkToken,
};

export default connect(mapStateToProps, mapDispatchToProps)(
  InitializationLayer);
